// 安全分数计算
const securityScore = computed(() => {
  let score = 0;
  
  // 基础分 60
  score += 60;
  
  // 邮箱已绑定 +10
  if (userInfo.email) score += 10;
  
  // 手机已绑定 +10
  if (userInfo.phone) score += 10;
  
  // 实名已认证 +20
  if (userInfo.isVerified) score += 20;
  
  return score;
});

// 安全分数颜色
const securityScoreColor = computed(() => {
  if (securityScore.value >= 80) return '#10b981'; // 绿色
  if (securityScore.value >= 60) return '#f59e0b'; // 黄色
  return '#ef4444'; // 红色
});

// 安全分数图标
const securityScoreIcon = computed(() => {
  if (securityScore.value >= 80) return CircleCheckFilled;
  if (securityScore.value >= 60) return SuccessFilled;
  return WarningFilled;
});

// 安全分数文字颜色
const securityScoreTextClass = computed(() => {
  if (securityScore.value >= 80) return 'text-green-600';
  if (securityScore.value >= 60) return 'text-amber-600';
  return 'text-red-600';
});

// 安全分数提示
const securityScoreTip = computed(() => {
  if (securityScore.value >= 80) return '您的账户安全状况良好';
  if (securityScore.value >= 60) return '您的账户安全状况一般，可以进一步提高';
  return '您的账户安全风险较高，请及时完善安全信息';
});

