});

// 验证码发送状态
const sendingEmailCode = ref(false);
const emailCodeCountdown = ref(0);
let emailCountdownTimer = null;

// 邮箱验证规则
const emailRules = {
  email: [
    { required: true, message: '请输入邮箱地址', trigger: 'blur' },
    { type: 'email', message: '请输入正确的邮箱地址', trigger: 'blur' }
  ],
  verifyCode: [
    { required: true, message: '请输入验证码', trigger: 'blur' },
    { pattern: /^\d{6}$/, message: '验证码为6位数字', trigger: 'blur' }
  ]
};

// 重置邮箱表单
const resetEmailForm = () => {
  emailForm.email = '';
  emailForm.verifyCode = '';
  if (emailFormRef.value) {
    emailFormRef.value.resetFields();
  }
};

// 发送邮箱验证码
const sendEmailCode = async () => {
  if (!emailForm.email) {
    ElMessage.warning('请先输入邮箱地址');
    return;
  }
  
  // 简单邮箱格式验证
  if (!/^[\w-]+(\.[\w-]+)*@[\w-]+(\.[\w-]+)+$/.test(emailForm.email)) {
    ElMessage.warning('请输入正确的邮箱地址');
    return;
  }
  
  sendingEmailCode.value = true;
  
  try {
    // 使用userApi发送验证码
    const response = await userApi.sendEmailCode(emailForm.email);
    sendingEmailCode.value = false;
    
    if (response && response.success) {
      // 启动倒计时
      emailCodeCountdown.value = 60;
      startCountdown();
      
      ElMessage({
        message: `验证码已发送至 ${emailForm.email}，请查收`,
        type: 'success',
        duration: 5000
      });
      
      // 如果后端返回了验证码（开发环境），显示在控制台
      if (response.code) {
        console.log('验证码:', response.code);
      }
    } else {
      throw new Error(response.message || '验证码发送失败，请稍后重试');
    }
  } catch (error) {
    console.error('发送验证码错误:', error);
    sendingEmailCode.value = false;
    
    ElMessage({
      message: error.message || '验证码发送失败，请检查网络连接',
      type: 'error',
      duration: 5000
    });
  }
};

// 处理倒计时
const startCountdown = () => {
  clearInterval(emailCountdownTimer);
  emailCountdownTimer = setInterval(() => {
    if (emailCodeCountdown.value > 0) {
      emailCodeCountdown.value--;
    } else {
      clearInterval(emailCountdownTimer);
    }
  }, 1000);
};

