// 紧急注销方法增加一个尝试测试路由
const emergencyDeactivate = async () => {
  try {
    console.log('开始紧急注销测试...');
    
    const token = localStorage.getItem('token');
    if (!token) {
      ElMessage.error('未找到登录令牌，请先登录');
      return;
    }
    
    // 显示加载状态
    const loading = ElLoading.service({
      lock: true,
      text: '正在处理注销请求...',
      background: 'rgba(0, 0, 0, 0.7)'
    });
    
    console.log('使用token:', token);
    
    // 获取用户ID
    let userId = '';
    try {
      // 尝试解析JWT令牌获取用户ID
      const tokenParts = token.split('.');
      if (tokenParts.length === 3) {
        const payload = JSON.parse(atob(tokenParts[1]));
        userId = payload.id;
        console.log('从token解析出用户ID:', userId);
      }
    } catch (parseError) {
      console.error('解析token出错:', parseError);
    }
    
    // 如果能获取用户ID，尝试使用测试路由
    if (userId) {
      try {
        console.log('尝试使用测试路由...');
        const testResponse = await fetch(`/api/auth/test-deactivate/${userId}`);
        
        if (testResponse.ok) {
          const data = await testResponse.json();
          console.log('测试路由响应:', data);
          
          if (data.success) {
            handleSuccessfulDeactivation(data.message || '账户已成功注销');
            loading.close();
            return;
          }
        }
      } catch (testError) {
        console.error('测试路由出错:', testError);
      }
    }
    
    // 尝试不同的API路径
    const apiEndpoints = [
      '/api/auth/deactivate',
      'http://localhost:8080/api/auth/deactivate',
      '/auth/deactivate'
    ];
    
    let success = false;
    
    for (const endpoint of apiEndpoints) {
      try {
        console.log(`尝试API端点: ${endpoint}`);
        const response = await fetch(endpoint, {
          method: 'DELETE',
          headers: {
            'Authorization': `Bearer ${token}`,
            'Content-Type': 'application/json'
          }
        });
        
        console.log(`端点 ${endpoint} 响应:`, response);
        
        if (response.ok) {
          let data;
          try {
            data = await response.json();
          } catch (e) {
            data = { message: '账户已成功注销' };
          }
          
          handleSuccessfulDeactivation(data.message || '账户已成功注销');
          success = true;
          break;
        } else {
          console.error(`端点 ${endpoint} 失败，状态:`, response.status);
          try {
            const errorText = await response.text();
            console.error('错误详情:', errorText);
          } catch (e) {
            console.error('无法读取错误详情');
          }
        }
      } catch (error) {
        console.error(`端点 ${endpoint} 请求出错:`, error);
      }
    }
    
    // 关闭加载状态
    loading.close();
    
    if (!success) {
      // 尝试最后的方法：使用window.location
      if (confirm('请求API失败，是否尝试直接刷新页面来注销账户？')) {
        userStore.clearUser();
        window.location.href = '/auth/login';
      } else {
        ElMessage.error('注销失败，请检查控制台日志或联系管理员');
      }
    }
  } catch (error) {
    console.error('紧急注销过程中发生错误:', error);
    ElMessage.error('注销失败，请稍后重试');
  }
};

// 处理成功注销的函数
const handleSuccessfulDeactivation = (message) => {
  // 清除用户数据
  userStore.clearUser();
  
  ElMessage({
    type: 'success',
    message: message,
    duration: 2000
  });
  
  // 跳转到登录页
  setTimeout(() => {
    router.push('/auth/login');
  }, 2000);
};

</script>

